"use strict"
/**
 * Theme for Highstock
 */
const Highstock = require("highcharts/highstock")

/**
 * Theme Colors
 */
const backgroundLight = "#edf2f9"
const black = "#111"
const formBackground = "#f8f9fb"
const greyLighter = "#fefefe"
const greyLight = "#e6e6e6"
const greyMedium = "#cccccc"
const greyDarker = "#666"
const highlight = "#7799d5"

/**
 * Main Theme
 */

Highstock.theme = {
  rangeSelector: {
    selected: 2,
    inputBoxBorderColor: greyLight,
    buttonTheme: {
      fill: formBackground,
      states: {
        hover: { fill: backgroundLight },
        select: { fill: highlight, style: { color: greyLighter } }
      }
    }
  },

  chart: {
    backgroundColor: greyLighter,
    style: {
      fontFamily: "'Source Sans Pro', arial, sans-serif",
      color: black
    }
  },

  xAxis: {
    lineColor: greyLight,
    tickColor: greyLight
  },

  yAxis: {
    lineColor: greyLight,
    tickColor: greyLight
  },

  navigator: {
    maskFill: "rgba(119, 153, 213, 0.07)",
    outlineColor: greyMedium,
    xAxis: { gridLineColor: greyLight },
    series: { color: highlight }
  },

  tooltip: { style: { fontSize: "14px" } },
  scrollbar: { enabled: false },
  credits: { enabled: false },

  plotOptions: {
    column: {
      color: greyDarker,
      dataGrouping: { enabled: false }
    },

    pie: {
      allowPointSelect: true,
      animation: false,
      cursor: "pointer",
      dataLabels: { style: { fontSize: "14px" } }
    },

    spline: {
      color: highlight,
      dataGrouping: { enabled: false }
    }
  }
}

Highstock.setOptions(Highstock.theme)

/**
 * Individual Theme Components
 */

Highstock.pieColors = [
  "rgba(231,  76,  60, 0.8)",
  "rgba(230, 126,  34, 0.8)",
  "rgba(242, 202,  39, 0.8)",
  "rgba(46,  204, 112, 0.8)",
  "rgba(52,  152, 219, 0.8)",
  "rgba(155,  89, 182, 0.8)",

  "rgba(231,  76,  60, 0.6)",
  "rgba(230, 126,  34, 0.6)",
  "rgba(242, 202,  39, 0.6)",
  "rgba(46,  204, 112, 0.6)",
  "rgba(52,  152, 219, 0.6)",
  "rgba(155,  89, 182, 0.6)",

  "rgba(231,  76,  60, 0.4)",
  "rgba(230, 126,  34, 0.4)",
  "rgba(242, 202,  39, 0.4)",
  "rgba(46,  204, 112, 0.4)",
  "rgba(52,  152, 219, 0.4)",
  "rgba(155,  89, 182, 0.4)"
]

Highstock.pieResponsive = {
  rules: [
    {
      condition: { maxWidth: 500 },
      chartOptions: {
        legend: { enabled: true },
        plotOptions: {
          pie: {
            showInLegend: true,
            dataLabels: {
              format: "{point.percentage:.0f}%",
              distance: -20
            }
          }
        }
      }
    }
  ]
}
